  font-style: normal;
  font-weight: 300;
  src: url("../fonts/MuseoSansCyrl300/museosanscyrl300.eot");
  src: url("../fonts/MuseoSansCyrl300/museosanscyrl300.eot?#iefix") format("embedded-opentype"), url("../fonts/MuseoSansCyrl300/museosanscyrl300.woff2") format("woff2"), url("../fonts/MuseoSansCyrl300/museosanscyrl300.woff") format("woff"), url("../fonts/MuseoSansCyrl300/museosanscyrl300.ttf") format("truetype"); }

@font-face {
  font-family: "Museo Sans Cyrl";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/MuseoSansCyrl500/museosanscyrl500.eot");
  src: url("../fonts/MuseoSansCyrl500/museosanscyrl500.eot?#iefix") format("embedded-opentype"), url("../fonts/MuseoSansCyrl500/museosanscyrl500.woff2") format("woff2"), url("../fonts/MuseoSansCyrl500/museosanscyrl500.woff") format("woff"), url("../fonts/MuseoSansCyrl500/museosanscyrl500.ttf") format("truetype"); }

*,
*::before,
*::after {
  box-sizing: border-box; }

html,
body {
  height: 100%; }

body {
  font-size: 16px;
  line-height: 1.4;
  color: #212121;
  font-family: "Museo Sans Cyrl", sans-serif;
  overflow-x: hidden;
  min-width: 320px; }
  body input,
  body textarea,
  body button {
    outline: none; }
    body input:focus,
    body textarea:focus,
    body button:focus {
      outline: none; }

.container {
  width: 1200px;
  margin-left: auto;
  margin-right: auto; }

.top-line {
  background-color: #2c3e50; }
  .top-line .top-line__content {
    display: flex;
    justify-content: space-between; }

.nav-top {
  display: flex;
  align-items: center; }
  .nav-top ul {
    list-style-type: none;
    padding: 0;
    margin: 0;
    display: flex; }
    .nav-top ul li a {
      display: block;
      padding: 12px 17px;
      color: #fff;
      text-decoration: none; }
      .nav-top ul li a:hover {
        text-decoration: underline; }
      .nav-top ul li a:active {
        opacity: 0.8; }
    .nav-top ul li .active {
      background-color: #577eb3;
      transition: color .25s ease, background .25s ease;
      border-radius: 4px; }
      .nav-top ul li .active:hover {
        background-color: #ffcd60;
        color: #577eb3;
        text-decoration: none; }

.summary__wrapper {
  background-color: #ffcd60;
  color: #2c3e50; }
  .summary__wrapper a {
    display: flex;
    align-items: center;
    text-decoration: none;
    color: #2c3e50;
    padding: 18px 10px;
    width: 100%;
    height: 100%;
    transition: all .25s ease;
    box-shadow: inset 0 0 10px 0px rgba(0, 0, 0, 0); }
    .summary__wrapper a:hover {
      box-shadow: inset 0 0 10px 0px rgba(0, 0, 0, 0.4); }
      .summary__wrapper a:hover .arrow--right {
        transform: translateX(5px); }
    .summary__wrapper a p {
      margin: 0;
      font-weight: 600;
      font-size: 12px;
      line-height: 14px;
      letter-spacing: .075px;
      text-transform: uppercase; }
    .summary__wrapper a .arrow--right {
      line-height: 1;
      fill: #2c3e50;
      transition: all .25s ease; }
      .summary__wrapper a .arrow--right svg {
        width: 15px;
        height: 24px;
        display: inline-block; }
  .summary__wrapper .summary__item {
    padding-left: 10px; }
    .summary__wrapper .summary__item b {
      display: block;
      white-space: nowrap;
      font-weight: 600;
      font-size: 18px;
      line-height: 22px;
      padding-right: 15px; }
      .summary__wrapper .summary__item b svg {
        width: 11px;
        height: 13px;
        display: inline-block;
        fill: #2c3e50; }
    .summary__wrapper .summary__item span {
      font-weight: 500;
      font-size: 10px;
      line-height: 12px;
      display: block;
      text-align: right;
      padding-right: 20px; }
      .summary__wrapper .summary__item span svg {
        width: 5.5px;
        height: 8px;
        fill: #2c3e50;
        display: inline-block; }

.cabinet {
  padding: 10px;
  text-align: right;
  color: #fff; }
  .cabinet p {
    margin: 0;
    font-size: 12px;
    line-height: 14px;
    letter-spacing: -.407px; }
  .cabinet a {
    color: #fff;
    text-decoration: none; }
    .cabinet a:hover {
      text-decoration: underline; }

.drop-list {
  margin-bottom: 10px; }
  .drop-list .current {
    font-size: 14px;
    line-height: 17px;
    letter-spacing: -.407px;
    position: relative;
    display: inline-block; }
    .drop-list .current:hover {
      cursor: pointer; }
    .drop-list .current::before {
      content: '';
      width: 10px;
      height: 6px;
      position: absolute;
      right: -5px;
      top: 50%;
      transform: translateY(-50%) translateX(100%);
      background-image: url("../img/icons/arrow-bottom.svg");
      background-repeat: no-repeat;
      background-position: center; }

.spirite-svg {
  display: none;
  opacity: 0;
  visibility: hidden; }
